body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f5f5f5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: #f5f5f5;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-direction: column;
  height: 100%;
}

/* LETTERS CONTAINER */
.letterContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  max-width: 650px;
}

/* WORD */
.the-word {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 30px;
}

.the-word p{
  display: flex;
  justify-content: center;
  align-items: center;

  width: 50px;
  height: 50px;
  background-color: #eee;
  color: #444;
  border: 1px solid rgba(0,0,0,0.1);
  border-bottom: 3px solid rgba(0,0,0,0.3);
  margin: 5px;
  font-size: 20px;
  font-family: "Montserrat SemiBold";
}

@media (max-width: 500px) {
  .the-word p {
    width: 30px;
    height: 30px;
  }
}

.the-word .matched {
  background-color: #00a8ff;
  color: #fff;
}

/* SCORE DISPLAY TEXT */
.score {
  font-family: "Montserrat Medium";
  color: #444;
  text-align: center;
  text-transform: uppercase;
}
.score span {
  color: #e1302a;
  font-family: "Montserrat SemiBold";
}

/* EXIT BUTTON */
.exit-btn {
  position: absolute;
  left: 0;
  top: 0;
  /* background-color: #00a8ff; */
  color: #444;
  padding: 5px;
  width: 70px;
}

@media (max-width: 840px) {
  .letterContainer {
    width: 100%;
  }
}
@font-face {
    font-family: "Montserrat SemiBold";
    src: url(/static/media/Montserrat-SemiBold.c88cecbf.ttf);
}

@font-face {
    font-family: "Montserrat Black";
    src: url(/static/media/Montserrat-Black.6d1796a9.ttf);
}

@font-face {
    font-family: "Montserrat Medium";
    src: url(/static/media/Montserrat-Medium.a98626e1.ttf);
}
/* LETTERS */
.letters {
    display: flex;
    justify-content: center;
    align-items: center;

    background-color: #fff;
    color: #333;
    border: 1px solid #cccccc90;
    box-shadow: 3px 3px 0px rgba(0,0,0,0.3);
    width: 50px;
    height: 50px;
    padding: 15px;
    margin: 5px;
    text-transform: uppercase;
    font-family: "Montserrat SemiBold";
    transition: 0.1s ease;
    cursor: pointer;
    outline: none;
}

.letters:hover {
  background-color: rgb(241, 241, 241);
  box-shadow: 3px 3px 0px rgba(0,0,0,0.3);
  color: #444;
}

.letters:active {
  box-shadow: 1px 1px 0px rgba(0,0,0,0.4);
}

.letters:disabled {
  color: #222;
  background-color: #ccc;
  box-shadow: 3px 3px 0px rgba(0,0,0,0.4);
} 
.letters:disabled:hover {
  background-color: #ccc;
}

@media (max-width: 500px) {
  .letters {
    width: 40px;
    height: 40px;
    padding: 0px;
  }
}
.game-over_menu {
    max-width: 450px;
    background-color: #fff;
    border: 1px solid #ccc;
    padding: 10px;
    text-align: center;

    position: absolute;
    top: 30%;
    left: 50%;
    -webkit-transform: translate(-50%, -30%);
            transform: translate(-50%, -30%);
    z-index: 9;
}

@media (max-width: 500px) {
    .game-over_menu {
        width: 80%;
    }
}

.game-over_menu h1 {
    font-family: "Montserrat Bold";
    color: #e1302a;
}

.game-over_menu p {
    color: #00a8ff;
    font-family: "Montserrat Medium";
}

.game-over_menu span {
    color: #e1302a;
    font-family: "Montserrat SemiBold";
}
.game-over_menu-buttons {
    display: flex;
    justify-content: center;
    width: 100%;
}

