.hang-container {
    width: 300px;
    height: 300px;
    position: relative;
    margin: 0 auto;
}

.hang-container .hang-ground {
    width: 0px;
    height: 10px;
    background-color: #444;
    position: absolute;
    bottom: 0;
    left: 0;
    -webkit-animation: hang-ground 0.3s ease forwards;
            animation: hang-ground 0.3s ease forwards;
}

@-webkit-keyframes hang-ground {
    0% {
        width: 0px;
    }
    100% {
        width: 130px;
    }
}

@keyframes hang-ground {
    0% {
        width: 0px;
    }
    100% {
        width: 130px;
    }
}

.hang-container .hang-body {
    width: 10px;
    height: 0px;
    background-color: #444;
    position: absolute;
    bottom: 0;
    left: 60px;
    -webkit-animation: hang-body 0.3s ease forwards;
            animation: hang-body 0.3s ease forwards;
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s;
}

@-webkit-keyframes hang-body {
    0% {
        height: 0px;
    }
    100% {
        height: 250px;
    }
}

@keyframes hang-body {
    0% {
        height: 0px;
    }
    100% {
        height: 250px;
    }
}

.hang-container .hang-top {
    width: 0px;
    height: 10px;
    background-color: #444;
    position: absolute;
    top: 40px;
    left: 60px;
    -webkit-animation: hang-top 0.3s ease forwards;
            animation: hang-top 0.3s ease forwards;
    -webkit-animation-delay: 0.6s;
            animation-delay: 0.6s;
}

@-webkit-keyframes hang-top {
    0% {
        width: 0px;
    }
    100% {
        width: 120px;
    }
}

@keyframes hang-top {
    0% {
        width: 0px;
    }
    100% {
        width: 120px;
    }
}

.hang-container .hang-top::after {
    content: "";
    width: 10px;
    height: 0px;
    position: absolute;
    right: 0;
    top: 0;
    background-color: #444;
    -webkit-animation: hang-top_handle 0.3s ease forwards;
            animation: hang-top_handle 0.3s ease forwards;
    -webkit-animation-delay: 0.9s;
            animation-delay: 0.9s;
}

@-webkit-keyframes hang-top_handle {
    0% {
        height: 0px;
    }
    100% {
        height: 30px;
    }
}

@keyframes hang-top_handle {
    0% {
        height: 0px;
    }
    100% {
        height: 30px;
    }
}

.hang-top .hangman {
    display: block;
    position: absolute;
    right: -45px;
    top: 32px;
    width: 100px;
    height: 200px;

    -webkit-animation: hanging 1.5s cubic-bezier(0.39, 0.58, 0.57, 1) infinite alternate;

            animation: hanging 1.5s cubic-bezier(0.39, 0.58, 0.57, 1) infinite alternate;
    -webkit-transform-origin: top center;
            transform-origin: top center;
}

@-webkit-keyframes hanging {
    0% {
        -webkit-transform: rotate(2deg);
                transform: rotate(2deg)
    }
    100% {
        -webkit-transform: rotate(-3deg);
                transform: rotate(-3deg)
    }
}

@keyframes hanging {
    0% {
        -webkit-transform: rotate(2deg);
                transform: rotate(2deg)
    }
    100% {
        -webkit-transform: rotate(-3deg);
                transform: rotate(-3deg)
    }
}

.hangman .hangman-head {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 5px solid #444;
    position: absolute;
    right: 23px;
    top: 0px;

    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hangman-head .hangman-eyes {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.hangman-head .hangman-eye {
    width: 8px;
    height: 8px;
    border-radius: 50px;;
    background-color: #444;
    margin: 0 5px;
}

.hangman-head .hangman-mouth {
    width: 15px;
    height: 3px;
    background-color: #444;
    margin-top: 5px;
    border-radius: 40px;
}

.hangman .hangman-torso {
    display: none;
    width: 5px;
    height: 70px;
    background-color: #444;
    position: absolute;
    right: 45px;
    top: 48px
}

.hangman .hangman-left_hand {
    display: none;
    width: 5px;
    height: 40px;
    background-color: #444;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);

    position: absolute;
    right: 30px;
    top: 45px;
}
.hangman .hangman-right_hand {
    display: none;
    width: 5px;
    height: 40px;
    background-color: #444;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);

    position: absolute;
    left: 35px;
    top: 46px;
}

.hangman .hangman-left_leg {
    display: none;
    width: 5px;
    height: 60px;
    -webkit-transform: rotate(20deg);
            transform: rotate(20deg);
    background-color: #444;
    position: absolute;
    left: 40px;
    bottom: 25px;
}
.hangman .hangman-right_leg {
    display: none;
    width: 5px;
    height: 60px;
    -webkit-transform: rotate(-20deg);
            transform: rotate(-20deg);
    background-color: #444;
    position: absolute;
    right: 35px;
    bottom: 25px;
}

.hangman .limbs {
    border-radius: 5px;
}
